// GratitudeEntries.tsx
import React, { useEffect, useState } from 'react';
import { useApi } from '../hooks/useApi';

interface User {
  username: string;
}

interface GratitudeEntry {
  id: number;
  content: string;
  user_id: User;
  created_at: string; // or Date if you parse it to a Date object
}

const GratitudeEntriesTest: React.FC = () => {
  const { fetchWithTokens } = useApi();
  const [entries, setEntries] = useState<GratitudeEntry[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<Error | null>(null);

  useEffect(() => {
    const getEntries = async () => {
      try {
        const data: GratitudeEntry[] = await fetchWithTokens('gratitude-entries/');
        setEntries(data);
        setLoading(false);
      } catch (err) {
        setLoading(false);
      }
    };

    getEntries();
  }, [fetchWithTokens]);

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error.message}</p>;

  return (
    <div>
      <h1>Gratitude Entries</h1>
      <ul>
        {entries.map(entry => (
          <li key={entry.id}>
            {entry.content} - {entry.user_id.username} ({new Date(entry.created_at).toLocaleString()})
          </li>
        ))}
      </ul>
    </div>
  );
};

export default GratitudeEntriesTest;
